    padding: 0;
  }
}
@media (max-width: 1200px) {
  .main-nav--opened .main-nav__top-container {
    background-color: #283645;
  }
}
.main-nav__toggle {
  position: relative;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
@media (min-width: 1200px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle {
  width: 50px;
  height: 24px;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
  opacity: 30%;
}
.main-nav--opened .main-nav__toggle {
  width: 23px;
  height: 23px;
  padding: 0;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  left: -3px;
  bottom: 10px;
  width: 29px;
  height: 3px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after {
  background-color: #d22856;
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: #d22856;
  opacity: 30%;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav__wrapper {
  position: relative;
  background-color: #283645;
  z-index: 1;
}
@media (min-width: 1200px) {
  .main-nav__wrapper {
    width: 620px;
    background-color: transparent;
  }
}
@media (max-width: 1199px) {
